import axios from 'axios';
// CancelToken ajax
// /defaults
const axiosConfig = () => {
    axios.defaults.baseURL = '/api';
    axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
    axios.defaults.timeout = 60000;
}
axiosConfig();

/***  */
const ajaxAftet = (result: any) => {
    return new Promise((resolve, reject) => {
        if (result.status === 200) {
            if (result.data.code === 0) {
                resolve(result.data);
            } else {
                reject(result.data);
            }
        } else {
            console.log('');
            reject(result.data);
        }
    });
};
/****
 * get    
 */
export const ajaxGet = (url: string, data: object) => {
    return axios.get(url, {
        params: data,
    }).then((result: object) => ajaxAftet(result));
};
/****
 * post    
 */
export const ajaxPost = (url: string, data: object) => {
    return axios.request({
        url,
        method: 'post',
        params: data,
        data,
    }).then((result: object) => ajaxAftet(result));
};


/**  */
export const ajaxUserBindWechatUser = (data: object) => ajaxPost('/api_proxy/user/bind_wechat_user/', data);
